@import "vars";

.survey-progress {
  position: fixed;
  top: 0;
  left: 0;
  width: 100% !important;
  background-color: $button-color;
  z-index: 2147483647;
  .survey-progress-bar {
    height: 0.5em;
    background-color: $progress-bar-color;
    span {
      display: none;
    }
    -webkit-transition: width 1s;
    transition: width 1s;
  }
}
@import "fonts";

$question-font-size: 1.1em;
$question-line-height: 1.5em;
$general-spacing: 0.5em;

$button-color: #f4f8fd;
$button-text-color: #478FE2;
$border-width: 0.15em;
$border-radius: 0.25em;

$break-small: 400px;
$break-large: 1200px;

$progress-bar-color: #9cb0d1;

$question-text-color: #000000;

$lightfont: "latolight", "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
$heavierfont: "latoregular", "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;


@import "vars";
@import "buttons";
@import "progress";
@import "fonts";

body {
    #surveyElement {
        font-family: $lightfont;
        max-width: 45em;
        font-size: $question-font-size;
        line-height: $question-line-height;
        margin-bottom: $general-spacing;
        -webkit-font-smoothing: subpixel-antialiased !important;
        padding-top: 5em;
        .panel-body {
            padding-top: 0;
            padding-bottom: 0;
            padding-left: 3em;
            padding-right: 3em;
            @media screen and (max-width: $break-small) {
                padding-left: 1.6em;
                padding-right: 1.0em;
            }
        }
        .qtitle {
            font-size: 1.2em;
        }
        .form-inline {
            @media screen and (max-width: $break-small) {}
        }
        .question {
            margin-top: 1em;
            b {
              font-weight: normal;
              font-family: $heavierfont;
            }
            h1 {
                font-family: $heavierfont;
                font-size: 1.3em;
                font-weight: normal;
                color: $question-text-color;
                position: relative;
                &::before {
                    position: absolute;
                    content: "â€»";
                    font-size: 1.5em;
                    top: -0.17em;
                    left: -1em;
                    color: lighten($question-text-color, 70%);
                    font-weight: lighter;
                    @media screen and (max-width: $break-small) {
                        font-size: 0.8em;
                        top: 0.22em;
                        left: -0.95em;
                        color: lighten($question-text-color, 80%);
                    }
                }
            }
            .radio input[type=radio],
            .checkbox input[type=checkbox] {
                display: none;
            }
            .radio,
            .checkbox {
                label {
                    @extend .surveyButton;
                    //width: 100%;
                    font-size: 0.8em;
                    margin-bottom: $general-spacing;
                    padding: $general-spacing;
                }
            }
        }
        .panel-footer {
            background-color: transparent;
            text-align: center;
        }
    }
}